.account {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}

.account h3 {
    margin: 0;
}

.accountContent {
    width: 100%;
}

.accountMain {
    display: grid;
    grid-template-columns: 4fr 1fr;
    grid-gap: 30px;
    margin-top: 50px;
}


.loginItems {
    display: flex;
    justify-content: space-between;
}

.loginToRegistration {
    width: 42%;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    padding: 20px;
}

.loginItems h4 {
    font-size: 20px;
    margin: 15px 0 15px 0;
}

.loginItems h5 {
    font-size: 16px;
}

.loginToRegistration button {
    min-width: 40%;
    height: 50px;
    margin-top: 15px;
}

.loginSection {
    width: 42%;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.loginItems a {
    color: #FFFFFF;
}

.loginSection input, .resetPassword input, .resetPassword input {
    width: 95%;
    padding: 10px;
    height: 25px;
    border: none;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    font-size: 18px;
    color: #1B130E;
}

.loginSection form div {
    margin-top: 20px;
}

.loginSection form div h5, .resetPassword form div h5 {
    margin-bottom: 5px;
}

.loginSection form {
    display: flex;
    flex-direction: column;
}

.loginSection form p, .errored {
    color: red;
    font-weight: 600;
}

.loginSection form a {
    font-size: 16px;
    text-decoration-line: underline;
    color: #1B130E;
    margin-top: 10px;
}

.loginSection button {
    width: 30%;
    margin-top: 30px;
    height: 50px;
}

/*sidebar*/
.accountSidebar {

}

.accountSidebar ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.accountSidebar li {
    text-align: center;
    margin-bottom: 10px;
    width: 100%;
    cursor: pointer;
}

.accountSidebar a {
    display: inline-block;
    font-size: 18px;
    color: #1B130E;
    width: 100%;
    height: 100%;
    padding: 10px;
}

/*registration*/
.registration h3 {
    margin-top: 0;
}

.registration h5 {
    font-size: 16px;
    margin-bottom: 5px;
}

.registration input, .editProfile input {
    border: none;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    width: 95%;
    height: 40px;
    font-size: 16px;
    margin-bottom: 20px;
    padding-left: 10px;
}

.registration button, .resetPassword button, .editProfile button {
    min-width: 30%;
    height: 50px;
    margin-top: 30px;
}

/*success registration*/
.successRegistration button {
    min-width: 25%;
    margin-top: 50px;
    height: 50px;

}

.successRegistration {
    min-height: 300px;
}

.successRegistration button a {
    color: #FFFFFF;
}

/*main profile*/
.accountProfile h3 {
    margin: 0;
}

.accountProfile h5 {
    margin-top: 30px;
    font-size: 20px;
}

.accountProfile ul {
    padding: 0;
}

.accountProfile li {
    margin-bottom: 10px;
}

.accountProfile strong {
    margin-right: 5px;
}

.accountProfile li a {
    font-size: 16px;
    text-decoration-line: underline;
    color: #1B130E;
}

/*change password*/
.resetPassword h3 {
    margin-bottom: 40px;
}

.resetPassword form div {
    margin-bottom: 30px;
}

.resetPassword form div h5 {
    margin-bottom: 5px;
}

.resetPassword form button {
    min-width: 25%;
    height: 50px;
}

/*adress*/
.adressItem, .ordersItem {
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    overflow: hidden;
    margin-top: 50px;
}

.adressTitles, .ordersTitles {
    display: flex;
    background: #CBC2BA;
    padding: 10px 0;
}

.adressTitles h5 {
    width: 50%;
    text-align: center;
    color: #FFFFFF;
    font-size: 18px;
}

.adressInfo, .ordersInfo {
    margin: 10px 5% 30px 5%;
    border-bottom: 1px solid #A2692F;
    display: flex;
    align-items: center;
}

.adressInfo div {
    width: 50%;
}

.adressInfo p {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}

.adressInfo div button {
    height: 45px;
    min-width: 35%;
}

.adressControls {
    display: flex;
    justify-content: space-around;
}

.adressControls a {
    color: #FFFFFF;
}

/*edit profile*/
.editProfile h3 {
    margin-bottom: 40px;
}

.editProfile form div {
    margin-bottom: 15px;
}

.requestSended {
    background: #CBF3BA;
    padding: 3px;
    border-radius: 30px;
    margin-bottom: 30px;
    max-width: 50%;
    display: flex;
    align-items: center;
}

.requestSended img {
    margin-left: 10px;
    margin-right: 10px;
}

/*orders*/
.ordersTitles h5 {
    width: 100%;
    text-align: center;
    color: #FFFFFF;
    font-size: 18px;
}

.ordersInfo ul {
    padding: 0;
}

.ordersInfo li {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
}

.ordersInfo li span {
    font-weight: 400;
}

.ordersInfoMain {
    margin: 10px 5% 30px 5%;
    align-items: center;
}

.ordersInfoMain ul {
    display: flex;
    padding: 0;
}

.ordersInfoMain li {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
}

.ordersInfoMain div {
    width: 100%;
    border-bottom: 1px solid #A2692F;
    margin-bottom: 30px;
}

.ordersInfoMain div ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

/*liked*/
.likedItems {
    background: #FFFFFF;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    overflow: hidden;
    margin-top: 50px;
}

.title ul {
    display: flex;
    padding: 10px 0;
    background: #CBC2BA;
    margin: 0;
}

.title li {
    width: 20%;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    color: #FFFFFF;
}

.likedInfo ul {
    display: flex;
    padding: 10px 0;
    margin: 0 0 30px 0;
    align-items: center;
    border-bottom: 1px solid #A2692F;
}

.likedInfo li {
    width: 20%;
    text-align: center;
    font-weight: 600;
    color: #1B130E;
}

.likedImg img {
    width: 85px;
    height: 85px;
}

@media (max-width: 978px) {
    .accountMain {
        grid-template-columns: 1fr;

    }

    .liked {
        width: 100%;
    }

    .accountMainPages {
        order: 1;
        display: flex;
        justify-content: center;
        margin: 20px 0 50px 0;
        width: 100%;
    }

    .accountSidebar {
        width: 90%;
        margin: 0 auto;
    }

    .likedItems {
        width: 100%;
    }

    .title li {
        font-size: 14px;
    }
    .loginItems{
        flex-direction: column;
        align-items: center;
    }
    .loginToRegistration{
        width: 85%;
        margin-bottom: 50px;
    }
    .loginSection{
        width: 85%;
        margin-bottom: 50px;

    }
}