.news {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 1;
}

.newsContent {
    width: 100%;
    min-height: 37vh;
}

.newsContent h3 {
    font-size: 20px;
}

.newsItem {
    display: flex;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    padding: 20px 10px 20px 10px;
    margin-bottom: 50px;
}

.newsItem img {
    width: 200px;
    height: 200px;
}

.itemInfo {
    margin-left: 3%;
}

.itemInfo h5 {
    font-size: 20px;
    margin-bottom: 20px;
}

.itemInfo span {
    font-size: 18px;
    color: #CBC2BA;

}

.itemInfo p {
    color: #1B130E;
    margin-bottom: 50px;
}

.itemInfo button {
    height: 50px;
    min-width: 25%;
}

.singleNewItem {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.singleNewItem img {
    width: 30%;
    height: 100%;
}

.singleNewInfo {
    margin-left: 5%;
    width: 70%;
}

.singleNewInfo p {
    font-size: 18px;
}

.singleNewInfo span {
    color: #CBC2BA;
}

.productIcons {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.productIcons div {
    background: #CBC2BA;
    padding: 15px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 25px;
}

.productIcons div:hover {
    filter: drop-shadow(0px 3px 10px #CBC2BA);
}

.productIcons div span {
    font-weight: bold;
    font-size: 16px;
    text-align: center;
    color: #FFFFFF;
    margin-left: 15px;
}

.productIcons div span span {
    margin-left: 0px;
}