.contacts {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 1;
}

.contactsContent {
    width: 100%;
    min-height: 37vh;
}

.contactsMain {
    margin-top: 50px;
    display: flex;
}

.contactsInfo {
    width: 50%;
}

.infoItem {
    display: flex;
    align-items: start;
}

.infoItem img {
    margin-right: 20px;
}

.infoItem p {
    width: 80%;
}

.contactsForm {
    width: 50%;
}

.contactsForm form {
    width: 100%;
}

.contactsForm form input, .contactsForm form textarea {
    height: 40px;
    border: none;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    width: 100%;
    padding: 0 10px;
    font-size: 16px;
    color: #1B130E;

}

.contactsForm form div {
    margin-bottom: 30px;
}

.contactsForm form h5 {
    margin-bottom: 7px;
}

.contactsForm form textarea {
    resize: none;
    padding: 5px 10px;
    min-height: 150px;
}
.contactsForm form button{
    height: 50px;
    min-width: 30%;
}


@media (max-width: 978px) {
    .contactsMain{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .contactsInfo{
        margin-bottom: 50px;
        width: 80%;
    }
    .contactsForm{
        margin-bottom: 50px;
        width: 80%;
    }
}