.brands {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 1;
}

.brandsContent {
    width: 100%;
}
.brandsmain{
    margin-bottom: 50px;
}

.brandsmain h3 {
    font-size: 20px;
}

.brandsmain h5 {
    font-size: 18px;
}

.brandLetter {
    margin-top: 40px;
}
.brandLinks {
    margin-top: 20px;
}
.brandLinks{
    display: flex;
}
.brandLinks a{
    font-size: 16px;
    text-decoration-line: underline;
    color: #1B130E;
    margin-right: 10%;
}