.info{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 1;

}

.infoContent{
    width: 100%;
}

.infoMain h5{
    font-size: 16px;
}
.infoMain ul{
    margin: 0 0 0 20px;
    padding: 0;
}
.infoMain li{
    list-style-type: disc;
}

.delivery h5{
    font-size: 18px;
}

.delivery h3,.security h3{
    font-size: 20px;
}
.delivery,.security{
    min-height: 35vh;
}