.product {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}

.productContent {
    width: 100%;

}

.productMain {
    display: flex;
    margin-top: 50px;
    width: 100%;
    box-shadow: 0px 3px 10px rgba(27, 19, 14, 0.15);
    border-radius: 15px;
}

.productMain img {
    padding: 20px;
    width: 30%;
    height: 30%;
}

.productInfo {
    margin: 20px 0 0 10px;
    display: flex;
    flex-direction: column;

}

.productInfo h3 {
    margin: 0;
    color: #1B130E;
    font-size: 18px;
    font-weight: bold;
}

.productRating {
    margin-top: 20px;
    display: flex;
    align-items: center;
}

.productRating a {
    cursor: pointer;
}

.productRating div {
    display: flex;
}

.productRating div p {
    margin: 0 0 0 15px;
    font-size: 16px;
    color: #1B130E;

}

.productRating a {
    margin-left: 100px;
    font-weight: 300;
    font-size: 16px;
    text-decoration-line: underline;
    color: #1B130E;
}

.productPrice {
    display: flex;
    margin-top: 20px;
    align-items: center;
}

.productPrice span {
    margin-left: 30px;
    color: #E6A151;
}

.productInfo h5 {
    margin-top: 20px;
    font-size: 16px;
}

.productInfo h5 span {
    font-weight: 300;
}

.productSize h5 {
    margin-bottom: 20px;
}

.productSize div {
    padding: 15px;
    box-shadow: 0px 3px 10px rgba(27, 19, 14, 0.15);
    border-radius: 30px;
    margin-bottom: 20px;
    cursor: pointer;
}

.productSize div:hover {
    box-shadow: inset 0px -2px 5px rgba(27, 19, 14, 0.15);
}

.productCount {
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 30px;
    width: 85px;
    padding: 10px;
}

.productCount span {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    color: #1B130E;
    width: 25px;
    margin-left: 7px;
}

.productCount div {
    display: flex;
    flex-direction: column;
}

.productCount img {
    width: 15px;
    height: 10px;
    padding: 10px 20px;
}

.productOrder {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 100%;
}

.productOrder button {
    margin-left: 20px;
    min-width: 40%;
    height: 50px;
}

.productIcons {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.productIcons div {
    background: #CBC2BA;
    padding: 15px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 25px;
}

.productIcons div:hover {
    filter: drop-shadow(0px 3px 10px #CBC2BA);
}

.productIcons div span {
    font-weight: bold;
    font-size: 16px;
    text-align: center;
    color: #FFFFFF;
    margin-left: 15px;
}

.productIcons div span span {
    margin-left: 0px;
}

.productTabs {
    margin-top: 75px;
}

.productTabs .productTabsList {
    display: flex;
    width: fit-content;
    padding: 0;
    margin: 0;
    background: #CBC2BA;
    border-radius: 15px 15px 0px 0px;
    overflow: hidden;
}

.productTabs .productTabsList li {
    font-weight: bold;
    font-size: 18px;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    padding: 10px 20px 10px 20px;
    cursor: pointer;
}

.productTadInfo {
    box-shadow: 0px 3px 10px rgba(27, 19, 14, 0.15);
    border-radius: 0px 15px 15px 15px;
    padding: 20px;
    min-height: 200px;
}

.tabActive {
    background: #A2692F;
}


.tabDescription p {
    font-size: 18px;
}

.tabDescription li {
    font-size: 18px;
    list-style-type: disc;
}

.tabCharacters ul {
    padding: 0;
    margin: 0;
}

.tabCharacters ul li {
    font-weight: bold;
    color: #1B130E;
    font-size: 18px;
    margin-bottom: 10px;
}

.tabCharacters ul li span {
    font-weight: 300;
}

.tabReview .reviewTitle {
    display: flex;
    align-items: center;
}

.tabReview .reviewTitle h3 {
    margin-right: 50px;
}

.tabReview .review p {
    margin: 0;
}

.review {
    margin-bottom: 30px;
    padding-bottom: 10px;
    border-bottom: 1px solid #A2692F;
}

.makeReview {
    margin-top: 75px;
}

.reviewForm div {
    margin-bottom: 30px;
}

.reviewForm div h5 {
    margin-bottom: 10px;
}

.reviewForm div input, .reviewForm div textarea {
    width: 99%;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    border: none;
    height: 40px;
    padding: 0 10px;
    font-size: 16px;
    color: #1B130E;
}

.reviewForm div textarea {
    resize: none;
    padding: 5px 10px;
    min-height: 150px;
}

.reviewForm button {
    min-width: 20%;
}

.deliveryInfo div {
    display: flex;
    align-items: center;
}

.deliveryInfo div h3 {
    margin-left: 20px;
}

.deliveryInfo p {
    margin: 0;
}

.deliveryInfo {
    margin-bottom: 20px;
}


/*mobile*/
@media (max-width: 1200px) {
    .productOrder button {
        min-width: 30%;

    }

}

@media (max-width: 978px) {
    .productMain {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .productMain img {
        width: 75%;
        height: 75%;
    }

    .productSize {
        max-width: 95%;
    }

    .productCount img {
        width: 15px;
        height: 10px;
        padding: 10px 20px;
    }

    .productIcons {
        justify-content: center;
    }

    .productTabs {
        margin-bottom: 50px;
    }

    .productTabs .productTabsList {
        background: #fff;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .productTabs .productTabsList li {
        background: #CBC2BA;
        width: 40%;
        border-radius: 20px;
        margin-bottom: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .productTabs .productTabsList li.tabActive {
        background: #A2692F;
    }

    .productTadInfo {
        border-radius: 15px;;
    }
}