.popup {
    z-index: 500;
    width: 100%;
    height: 2000px;
    position: absolute;
    display: flex;
    justify-content: center;

}

.back {
    background: #000;
    opacity: 0.5;
    width: 100%;
    height: 100%;
    position: absolute;
}

.text {
    background: #FFFFFF;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    opacity: 1 !important;
    margin-top: 40vh;
    width: 30%;
    height: 150px;
    z-index: 700;
    font-size: 18px;
    font-weight: 600;
    padding: 10px;
}
.turnoff{
    display: flex;
    justify-content: flex-end;
}
.turnoff img{
    width: 15px;
    height: 15px;
}
.text p {
    text-align: center;
}